<template>

  <main>
    <div class="px-4 py-6 sm:px-0">
      <div
          class="p-4 text-center text-gray-400 border-4 border-gray-200 border-dashed rounded-lg  h-96"
      >
        <h1>Login</h1>
        <div>
          <label for="email">Email</label>
          <input type="email" v-model="user.email">
        </div>
        <div>
          <label for="password">Password</label>
          <input type="password" v-model="user.password">
        </div>
        <button @click="login">Login</button>
      </div>
    </div>
  </main>


  <div>

  </div>
</template>

<script>
import Auth from '../Auth.js';

export default {
  data() {
    return {
      user: {
        email: '',
        password: '',
      }
    };
  },

  methods: {
    login() {
      this.axios.post('http://127.0.0.1:8000/api/login', this.user)
          .then(({data}) => {
            Auth.login(data.access_token,data.user); //set local storage
            this.$router.push('/dashboard');
          })
          .catch((error) => {
            console.log(error);
          });
    }
  }
}
</script>